// Relies on Custom Event API built into most browsers
// https://developer.mozilla.org/en-US/docs/Web/API/CustomEvent
export var createEventHandler = function (eventName) {
    return {
        emit: function (data) { return document.dispatchEvent(new CustomEvent(eventName, { detail: data })); },
        connect: function (callback) {
            var connectionHandler = function (event) {
                callback(event.detail);
            };
            document.addEventListener(eventName, connectionHandler);
            return function () {
                document.removeEventListener(eventName, connectionHandler);
            };
        },
    };
};
